@import "./reset.css";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');



/* * {
    border: 1px solid red;
} */

:root {
    --darkblack: #000;
    --black: #171718;
    --black-border: #26292D;
    --white: #fff;
    --purple: #5C62EC;
    --text-color: var(--black);

    --dark-bg: var(--black);
    --dark-border: var(--black-border);
    --header-text: var(--white);
    --accent: var(--purple);

    /* Light mode */
    --page-bg: var(--white);
    --text-color: var(--black);

    --title-1: var(--accent);

    --project-card-bg: var(--white);
    --project-card-text: var(--black);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

.dark {
     /* Dark mode */
    --page-bg: #0B0C1C;
    --text-color: var(--white);

    --title-1: var(--white);

    --project-card-bg: var(--black);
    --project-card-text: var(--white);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);
}

html, body {
   
    min-height: 100vh;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: -0.5px;
    background-color: var(--page-bg);
    color: var(--text-color);
}

#root{
    min-height: 100vh;
}
.App{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* General */

.container {
    margin: 0 auto;
    padding: 0 1rem;
    max-width: 1200px;
}

.none {
    display: none !important;
}

.section {
    padding: 4.3rem 0;
}

.title-1 {
    margin-bottom: 3.75rem;
    font-size: 60px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--title-1);
    text-align: center;
}

.title-2 {
    margin-bottom: 1.25rem;
    font-size: 2.5rem;
    font-weight: 700;
    line-height: 1.3;
}

/* Btn */

.btn {
    display: inline-block;
    height: 48px;
    padding: 12px 28px;
    border-radius: 5px;
    background-color: var(--accent);
    color: var(--white);
    letter-spacing: 0.15px;
    font-size: 1rem;
    font-weight: 500;
    transition: opacity 0.2s ease-in;
}

.btn:hover {
    opacity: 0.8;
}

.btn:active {
    position: relative;
    top: 1px;
}

/* Btn outline */

.btn-outline {
    display: flex;
    column-gap: 0.6rem;
    align-items: center;
    height: 3rem;
    padding: 0.75rem 1.25rem;
    border-radius: 5px;
    border: 1px solid var(--darkblack);
    background-color: var(--white);
    color: var(--darkblack);
    transition: opacity 0.2s ease-in;
}

.btn-outline:hover {

    background-color:#E5E5E5;
}

.btn-outline:active {
    position: relative;
    top: 1px;
}

/* Projects  */

.projects {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    column-gap: 1.8rem;
    row-gap: 1.8rem;
}



/* Project details */

.project-details {
    margin: 0 auto;
    max-width: 865px;

    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;
}

.project-details__cover {
    max-width: 100%;
    margin-bottom: 2.5rem;

    box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.project-details__desc {
    margin-bottom: 1.875rem;

    font-weight: 700;
    font-size: 24px;
    line-height: 1.3;
}

/* Content list */

.content-list {
    margin: 0 auto;
    max-width: 570px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 2.5rem;
    text-align: center;
}

.content-list a {
    color: var(--accent);
}

.content-list__item {
    font-size: 18px;
    line-height: 1.5;
}

.content-list__item p + p {
    margin-top: 0.5em;
}
/* about */

.about__wrapper{
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    margin-bottom: 1rem;
    flex-wrap: wrap;
}

.about__text {
 max-width: 430px;
 text-align: right;
font-size: 1.2rem;
}
.about__skills {
    padding: 4.3rem 0;
}
.skills__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
}
.skills__item {
    height: 7.5rem;
    width: 7.5rem;
    background:  var(--dark-bg);
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.47);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;;
    color: var(--white);
  
}
.skills__item span{
    margin-top: 1rem;
    display: block;
    font-size: 1.2rem;
    text-transform: uppercase;
}


@media (max-width: 620px) {

    /* Header  */

    .header {
        min-height: unset;
    }

    .header__title {
        font-size: 1.6rem;
    }

    .header__title strong {
        font-size: 2rem;
    }

    /* Nav */

    .nav-row {
        justify-content: space-between;
    }

    /* General */

    .section {
        padding: 2.5rem 0;
    }

    .title-1 {
        margin-bottom: 1.8rem;
        font-size: 2.5rem;
    }

    .title-2 {
        margin-bottom: 10px;
        font-size: 1.875rem;
    }

    /* Projects */

    .project__title {
        font-size: 1.3rem;
        line-height: 1;
    }

    /* Project page */

    .project-details__desc {
        margin-bottom: 1.25rem;
        font-size: 1.3rem;
    }

    /* Content List */

    .content-list {
        row-gap: 1.25rem;
    }

    .content-list__item {
        font-size: 1rem;
    }
    .about__text{
        font-size: 1rem;
    }

    .about__skills{
        padding: 1rem 0;
    }
    .skills__list{
        grid-template-columns: repeat(3, 1fr);
    }
    .skills__item{
        height: 5rem;
        width: 5rem; 
    }
    .skills__item img{
        width: 2.5rem;
        height: 2.5rem;
    }
    .skills__item span{
        font-size: 0.8rem;
        margin-top: 0.5rem;
    }

    /* Footer */

    .footer {
        padding: 2.5rem 0 1.875rem;
    }

    .footer__wrapper {
        row-gap: 1.25rem;
    }

    /* Social */

    .social {
        column-gap: 1.25rem;
    }

    .social__item {
        width: 1.7rem;
    }

}